<template>
  <div class="flex">
    <div id="chart" class="row">
      <div id="chartk" class="col-xs-12 col-md-6">
        <p>Ventas de la semana pasada</p>
        <apexchart type=heatmap :width="window.width" height=350 :options="chartOptions" :series="dataChart"/>
      </div>
      <ChartWeek :dataChartWeek="dataChartWeek" :width="window.width"/>
    </div>
  </div>
</template>

<script>
import helperConfig from '../helpers/config'
import VueApexCharts from 'vue-apexcharts'
import axios from 'axios'
import ChartWeek from './ChartWeek'
export default {
  name: 'ChartPrincipal',
  components: {
    apexchart: VueApexCharts,
    ChartWeek,
  },
  data() {
    return {
      chartOptions: {
        dataLabels: {
          enabled: false
        },
        colors: ["#62cb31"]
      },
      dataChart: [],
      dataChartWeek: [
      ],
      window: {
        width: 0
      }
    }
  },
  created() {
    window.addEventListener('resize', this.handleResize)
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    getConfig: helperConfig.getConfig,
    handleResize() {
      this.window.width = window.innerWidth * .45
    },
    generateData(count, yrange) {
      let i = 0
      let series = []
      while (i < count) {
        let x = '7am'
        let y = Math.floor(Math.random() * (yrange.max - yrange.min + 1)) + yrange.min

        series.push({
          x: x,
          y: y
        })
        i++
      }
      return series
    },
    async getDataOnline() {
      try {
        const res = await axios(this.getConfig('get', '/dashboard'))
        console.log(res)
        //let dayRestant = new Date().getDay()
        this.getDataChart(res.data.sale_of_month)
        this.dataChartWeek = res.data.sale_of_days
      } catch (error) {
        //console.log(error)
      }
    },
    getDataChart(data) {
      console.log(data)
      const auxData = data.map((a) => {
        console.log(a)
        a.data.map((b) => {
          b.x = b.time
          b.y = b.value
        })
        a.name = a.day
        return a
      })
      console.log(auxData)
      //this.dataChart = auxData.slice(from, 7 + from)
      this.dataChart = auxData
    }
  },
  mounted() {
    this.getDataOnline()
  }
}
</script>

<style scoped>
  #chartk {
    font-family: sans-serif;
    text-align: center;
  }

  #chart {
    display: flex;
  }

  @media (max-width: 600px) {
    #chart {
      display: block;
    }
  }
</style>
